.catalog {
  margin: 50px 40px;
  display: grid;
  grid-template-columns: repeat(4, 200px);
  gap: 46px 48px;

  justify-content: center;

  @media screen and (min-width: 768px) and (max-width: 1023px) {
    grid-template-columns: repeat(3, 200px);
  }

  @media screen and (min-width: 488px) and (max-width: 767px) {
    grid-template-columns: repeat(2, 200px);
  }

  @media screen and (max-width: 487px) {
    grid-template-columns: repeat(1, 200px);
  }
}

.card {
  width: $widthElemets;
  padding: 32px 16px 16px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  transition-duration: $defaultDuration;

  &:hover {
    transform: scale(1.2);
  }

  &__image {
    display: block;
    height: 134px;
    margin: 0 3px 40px;
  }

  &__title {
    white-space: nowrap;
    width: 100%;
    font-size: 12px;
    color: $colorReview;
    font-weight: 500;
    line-height: 18px;
    transition-duration: $defaultDuration;

    &:hover {
      color: #34568b;
      cursor: pointer;
    }
  }

  &__product {
    font-size: 10px;
    color: #616070;
    margin-top: 4px;
    line-height: 14px;
  }

  &__stars {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-top: 16px;
    margin-bottom: 24px;
  }

  &__reviews {
    font-size: 10px;
    color: $colorReview;
    font-weight: 400;
    line-height: 14px;
    margin-left: 17px;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    width: 100%;
    margin-bottom: 16px;
  }

  &__price-txt {
    font-size: 12px;
    font-weight: 400;
    color: #616070;
    line-height: 18px;
  }

  &__costs {
    font-size: 16px;
    font-weight: 700;
    color: $colorReview;
    line-height: 18px;
  }

  &__button {
    border: 1px solid $colorButton;
    background-color: $colorButton;
    width: 100%;
    height: 40px;
    border-radius: 5px;
    color: $colorButtonWhite;
    font-weight: 700;
    font-size: 14px;
    text-decoration: none;
    line-height: 16px;
    align-items: center;
    text-transform: uppercase;
    text-align: center;
    display: flex;
    justify-content: center;
    transition-duration: $defaultDuration;

    &:hover {
      background-color: $colorButtonWhite;
      color: $colorButton;
    }
  }
}
